.webform {
  max-width: 768px;
  margin-right: auto;
  margin-left: auto;

  > fieldset:not(:first-child) {
    margin-bottom: 2 * $spacer;
    padding: $spacer $side_gap;

    border-width: 2px;
    border-color: $link-hover-color;

    @extend .card;
  }

  legend {
    width: unset;
    padding: 0 0.5rem;

    color: $link-hover-color;
  }
  .form-item {
    @extend .custom-control;
  }
  .form-type-radio {
    @extend .custom-radio;
  }
  .form-type-checkbox {
    @extend .custom-checkbox;
  }
  .form-number {
    display: initial;
    width: unset;

    @extend .form-control;
  }
  .custom-file-label::after {
    content: ". . .";
  }
  .description {
    font-size: $small-font-size;
  }
  .error {
    background-image: none;
  }
  .messages {
    padding: $spacer $side_gap;
  }
  .remove-button {
    @extend .btn;
    @extend .btn-danger;
  }
  .file-icon {
    margin: 0;
  }
  .form-actions {
    position: sticky;
    bottom: 0;

    display: none;
    margin: 0 (-$side_gap);
    padding: $spacer $side_gap 0;

    background-color: white;
    border-top: 2px dashed $secondary;

    .form-submit {
      font-size: 1.5rem;

      @extend .btn;
      @extend .btn-secondary;
    }
  }
  .total__summary {
    flex-basis: 100%;

    font-size: $small-font-size;
  }
  .total__sum {
    font-size: 1.5rem;
    line-height: 3rem;
  }
  .total__price {
    color: red;
  }
}

.webform-component__wrap {
  > :not(:last-child) {
    margin-bottom: 1rem;
  }
}

.webform-component {
  margin-bottom: $spacer;

  &--parameters,
  &--customer {
    .webform-component__wrap {
      align-items: flex-end;

      @extend .row;
      > * {
        @extend .col-sm-6;
      }
    }
  }
  &--parameters--comment,
  &--parameters--amount,
  &--customer--consent,
  &--customer--terms {
    flex-basis: 100% !important;
    max-width: 100% !important;
  }
  &--parameters--amount {
    select {
      width: unset;
      vertical-align: baseline;
    }
    .description {
      display: inline-block !important;
      margin-left: 1em;
    }
  }
  &--option {
    .form-radios {
      display: flex;
      flex-wrap: wrap;
      margin-right: -$grid-gutter-width/2;
      margin-left: -$grid-gutter-width/2;

      .form-item {
        padding-right: $grid-gutter-width/2;
        padding-left: $grid-gutter-width/2;
      }

      > * {
        flex-grow: 1;
        margin: 0;
        margin-bottom: $spacer;
      }

      input[type="radio"] {
        display: none;
      }

      label {
        position: relative;

        display: block;
        height: 100%;
        padding: 2rem 1rem;

        font-size: 1.5rem;
        text-align: center;

        background-color: $gray-200;
        border: 2px solid $gray-500;
        border-radius: 0.25rem;
        box-shadow: 0 0 0.5rem $gray-500;
        cursor: pointer;

        transition: 0.3s;

        &::before,
        &::after {
          display: none;
        }
      }

      label:hover,
      .form-radio:checked + label {
        color: white !important;

        background-color: $link-hover-color;
        border-color: $link-hover-color;
        box-shadow: 0 0 0.5rem $link-hover-color;
      }
    }
  }
}

.webform--active {
  .webform-component {
    &--intro {
      display: none;
    }
    &--design {
      border-top-left-radius: 0 !important;
      border-top-right-radius: 0 !important;
    }
    &--option {
      margin-bottom: 0;
      padding: 0;

      border: none;

      .form-radios {
        flex-wrap: unset;
        > * {
          margin-bottom: 0;
        }

        label {
          padding: 0.5rem;

          font-size: 1rem;
          color: $gray-500;

          border-bottom: transparent;
          border-bottom-right-radius: 0;
          border-bottom-left-radius: 0;

          &:not(:hover) {
            box-shadow: none;
          }
        }
      }
    }
  }

  .form-actions {
    z-index: 1;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-end;
    padding-bottom: $spacer;

    @media (min-width: map-get($grid-breakpoints, "md")) {
      justify-content: space-between;
    }
  }
}

.logged-in .form-actions {
  bottom: 40px;
}
