.catalog__items {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: $side-gap;
  margin: $side-gap;
}

.catalog__item {
  background-color: white;
  border-radius: 0.5rem;
  box-shadow: $box-shadow;
  padding: $side-gap;
}

.catalog__product-name {
  font-size: 1.5rem;
}
