@keyframes animatedBackground {
  0% {
    background-position: calc(100% + 400px) calc(50% + 15px);
  }
}

.banner {
  position: relative;

  min-height: 200px;
  overflow: hidden;

  color: #ffffff;
  text-shadow: 0 0 5px black;

  background-color: $primary;
  background-image: url("../img/belka-printoffice-bw.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.banner__wrap {
  @extend .container;
}

.banner__title-image {
  position: absolute;
  left: 0;

  display: none;
  width: 100%;
  min-height: 200px;

  background-repeat: no-repeat;
  background-position: calc(50% + 200px) calc(50% + 15px);
  background-size: 400px, auto;
  opacity: 1;

  animation-name: animatedBackground, opacityUp;
  animation-duration: 2s;
  animation-timing-function: ease-out;
  animation-iteration-count: 1;

  @extend .d-md-block;
}

.banner::before {
  content: "";
  position: absolute;

  width: 100%;
  height: 100%;

  background-color: $primary;
  opacity: 0.6;

  animation: opacityDown 2s ease-in 1;
}

.banner a {
  color: inherit;
  &:hover,
  &:focus {
    color: $link-hover-color;
  }
}

.banner__title {
  position: relative;

  margin-bottom: 1rem;
  padding-top: 2rem;
  overflow: hidden;

  font-style: normal;
  font-weight: bold;
  font-size: 2rem;

  opacity: 1;

  animation: opacityUp 1s ease-in 1;

  @media (min-width: map-get($grid-breakpoints, "sm")) {
    font-size: 3rem;
  }
}

.banner__slogan {
  position: relative;

  margin-bottom: 2rem;
  padding: 0;

  font-size: 1.5rem;

  opacity: 1;

  animation: opacityUp 1.5s ease-in 1;

  @extend .col-md-8;
}
