.content {
  opacity: 1;

  animation: opacityUp 0.5s 1;

  img {
    margin-bottom: $spacer;
  }

  h2 {
    margin-bottom: 1rem;

    color: $primary;
    column-span: all;
  }

  h3 {
    margin-top: 2rem;
    margin-bottom: $paragraph-margin-bottom;

    font-size: 1.5rem;
    // color: $primary;
    column-span: all;
  }

  p {
    max-width: 600px;
  }

  em {
    font-style: normal;
    color: $emphasis-color;
  }

  ul {
    margin-top: -$paragraph-margin-bottom;
    margin-bottom: $paragraph-margin-bottom;
    padding-left: 1rem;

    list-style: none;

    li::before {
      content: "";
      position: relative;
      bottom: 1px;

      display: inline-block;
      width: 0.5em;
      height: 0.5em;
      margin-right: 0.5em;
      margin-left: -1rem;

      background-color: $primary;
      border-radius: 50%;
    }
  }

  .price {
    font-size: 1.5rem;
    line-height: 1em;
    color: $emphasis-color;
  }
}

.content__wrap {
  padding: $spacer $side-gap;

  background-color: white;

  @extend .container;
}

.content__section {
  &::before {
    content: "";

    display: block;
    width: 66%;
    height: 2px;
    margin: 2rem auto;

    background: radial-gradient($link-hover-color, transparent);
  }
}

.content__columns {
  column-count: 2;
  column-gap: 2rem;
  column-rule: 1px solid lightgray;

  @media (max-width: 991px) {
    column-count: 1;
  }

  & li {
    display: block;

    break-inside: avoid;
  }
}

.content__2col-images {
  display: flex;
}

.content__gallery {
  display: grid;

  gap: $side-gap;
  grid-template-columns: repeat(auto-fit, minmax(215px, 1fr));

  a {
    align-self: center;

    box-shadow: $box-shadow;

    justify-self: center;
  }
}
.content__flex {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: $spacer;

  > * {
    @extend .col-md-6;
  }
}

.content__button {
  display: block;
  align-self: center;
  padding: 0.75rem;

  font-size: 2rem;
  line-height: 1;

  @extend .btn;
  @extend .btn-primary;
}

.content__table {
  width: 100%;
  overflow-x: auto;
}

.content__intro {
  img {
    float: right;
    max-width: map-get($grid-breakpoints, "lg") / 2 - $side-gap * 2;
    margin-left: $side-gap;
  }
  @media (max-width: map-get($grid-breakpoints, "lg") - 1px) {
    display: flex;
    flex-wrap: wrap;
    img {
      max-width: 100%;
      margin-left: 0;
    }
    :nth-child(2) {
      order: -1;
    }
  }
}
