.anons {
  display: flex;
  margin-bottom: 3rem;

  @extend .col-sm-6;
  @extend .col-md-4;
  @extend .col-lg-3;

  @media (max-width: map-get($grid-breakpoints, "sm") - 1px) {
    max-width: $max-col-width + (2 * $side-gap);
    margin-right: auto;
    margin-left: auto;
  }

  img {
    width: 100%;
    margin-bottom: $paragraph-margin-bottom;
  }
}

.anons__wrap {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 1rem;
  padding-bottom: 0;
  overflow: hidden;

  background-color: white;
  border-radius: 0.5rem;
  box-shadow: $box-shadow;

  transition: all 0.5s;

  &:hover {
    box-shadow: 0 .5rem 1rem rgba($black, .5);
  }
}

.anons__heading {
  font-size: $h3-font-size;
  color: $primary;
}

.anons__readmorebutton {
  width: calc(100% + 2rem);
  margin: auto -1rem 0;
  padding: 0.5rem 1rem;

  line-height: 45px;

  border-radius: 0;

  @extend .btn;
  @extend .btn-primary;
  @extend .text-truncate;
}
