.main-menu {
  display: block;
  margin-bottom: 0;
  padding-left: 0;

  list-style: none;

  background-color: $primary;

  @media (min-width: map-get($grid-breakpoints, "sm")) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  @media (min-width: map-get($grid-breakpoints, "md")) {
    justify-content: left;

    background-color: white;
  }
  @media (min-width: map-get($grid-breakpoints, "lg")) {
  }
}

.main-menu__item {
  position: relative;

  background-color: inherit;

  transition: all 0.3s;

  &:hover {
    background-color: darken($primary, 7.5%);
    // z-index: 1;
    // box-shadow: $button-box-shadow;
    @media (min-width: map-get($grid-breakpoints, "md")) {
      background-color: $primary;
    }
  }
}

.main-menu__link {
  position: relative;

  display: block;
  width: 100%;
  padding: 0.5rem 1rem;

  text-align: center;
  color: $primary;
  text-transform: uppercase;
  white-space: nowrap;

  background: none;
  border: none;

  &.active {
    color: $over-primary;
  }

  @media (min-width: map-get($grid-breakpoints, "md")) {
    text-align: left;
    text-transform: unset;
    &:hover,
    &.active:hover {
      color: $over-primary;
    }
    &.active {
      color: $link-active-color;
    }
  }

  @media (max-width: map-get($grid-breakpoints, "md") - 1px) {
    color: $over-primary;
    &:hover {
      color: $over-primary;
    }
  }
}
