.table {
  width: unset;

  text-align: center;

  border: (2 * $table-border-width) solid $table-border-color;

  caption {
    padding-top: $table-cell-padding;
    padding-bottom: $table-cell-padding;

    caption-side: top;
    font-weight: bold;
    text-align: left;
    color: $secondary;
  }

  th,
  td {
    border-bottom: $table-border-width solid $table-border-color;
  }

  th:first-child {
    vertical-align: middle;
    text-align: left;
  }
  tbody td:hover {
    outline: red 1px solid;
    cursor: pointer;
  }
  tbody tr:hover {
    td,
    th:nth-last-of-type(1) {
      background-color: rgba(255, 0, 0, 0.1);
    }
    th:nth-last-of-type(1) {
      color: red;
    }
  }
  .highlighted {
    background-color: rgba(255, 0, 0, 0.1) !important;
  }
  th.highlighted {
    color: red;
  }
}

.table__cg-0 {
  background-color: scale-color($secondary, $lightness: 95%);
}

.table__cg-1 {
  background-color: scale-color($orange, $lightness: 95%);
}

.table__cg-2 {
  background-color: scale-color($primary, $lightness: 95%);
}
