.main-contacts {
  display: flex;
  a {
    margin-left: 15px;

    line-height: 40px;

    background-repeat: no-repeat;
    background-position: left center;
    svg {
      position: relative;
      bottom: 2px;

      width: auto;
      height: 15px;
      margin-right: 4px;

      fill: $primary;
    }
    &:hover svg {
      fill: $link-hover-color;
    }
  }
}

@mixin contact {
  display: none;

  background-size: auto 20px;
}

.main-contacts__tel {
  @extend .d-sm-block;
  @include contact;
}

.main-contacts__geo {
  @extend .d-lg-block;
  @include contact;
}

.main-contacts__mail {
  @extend .d-md-block;
  @include contact;
}
