.social-links {
  display: flex;
  justify-content: space-around;;
  margin-top: auto;
  margin-bottom: auto;
  padding: 0;
}

.social-links__item {
  display: block;
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 5px;

  border-radius: 12px;

  a {
    display: block;
    width: 100%;
    height: 100%;
  }

  svg {
    fill: white;
  }

  &--facebook {
    padding: 0;

    background: center no-repeat linear-gradient(to bottom, #0062e0, #19afff);
    & svg {
      width: 100%;
      height: 100%;
    }
  }

  &--vk {
    background-color: #4680c2;
  }

  &--telegram {
    background-color: #27a7e5;
  }

  &--instagram {
    background: center no-repeat
    radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285aeb 90%);
  }
}
